@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+Display:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Sarabun:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap");
html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Sarabun", sans-serif;
}

h1, h2, h3, h4, h5 {
  color: rgb(80, 80, 80);
}

.container {
  max-width: 1240px;
  padding: 0 20px;
  margin: 0 auto;
}

blockquote {
  font-style: oblique;
  position: relative;
  background-color: rgba(236, 236, 236, 0.3019607843);
  padding: 40px;
  margin: 20px 0;
  border-radius: 5px;
  font-size: 20px;
}
blockquote p {
  display: inline;
}
blockquote::before {
  position: absolute;
  content: open-quote;
  font-size: 50px;
  margin-left: -0.6em;
  margin-top: -0.4em;
  color: rgba(0, 0, 0, 0.507);
}
blockquote:after {
  position: absolute;
  content: close-quote;
  font-size: 50px;
  bottom: 0;
  right: 0;
  margin-right: 20px;
  margin-bottom: 0;
  color: rgba(0, 0, 0, 0.514);
}

.edrea-main h1 {
  font-size: 38px;
  font-weight: 600;
}
.edrea-main h2 {
  font-size: 28px;
  font-weight: 600;
}
.edrea-main h3 {
  font-size: 23px;
  font-weight: 600;
}
.edrea-main h4 {
  font-size: 18px;
  font-weight: 600;
}
.edrea-main ul, .edrea-main ol {
  margin: 0;
  padding: 0;
  padding-left: 20px;
}
.edrea-main a {
  text-decoration: none;
  color: #005792;
  font-weight: 600;
  transition: 0.3s;
}
.edrea-main a:hover {
  color: #e77221;
}

.edrea-archive-blog {
  margin: 60px 0;
}
.edrea-archive-blog .swiper {
  margin-bottom: 60px;
}
.edrea-archive-blog .swiper .swiper-button-next, .edrea-archive-blog .swiper .swiper-button-prev {
  top: unset;
  border: 1px solid #005792;
  padding: 5px 15px;
  color: #e77221;
  border-radius: 3px;
  bottom: 0;
  width: 25px;
  height: 25px;
  transition: 0.3s;
}
.edrea-archive-blog .swiper .swiper-button-next::after, .edrea-archive-blog .swiper .swiper-button-prev::after {
  font-size: 14px;
}
.edrea-archive-blog .swiper .swiper-button-next:hover, .edrea-archive-blog .swiper .swiper-button-prev:hover {
  background-color: #005792;
  color: #FFF;
}
.edrea-archive-blog .swiper .swiper-button-prev {
  left: unset;
  right: 100px;
}
.edrea-archive-blog .swiper .swiper-button-next {
  right: 40px;
}
.edrea-archive-blog__slider--item {
  display: flex !important;
  align-items: flex-start;
  justify-content: space-between;
}
.edrea-archive-blog__slider--item .trending-thumbnail {
  width: calc(50% - 30px);
}
.edrea-archive-blog__slider--item .trending-thumbnail img {
  width: 100%;
  object-fit: cover;
  height: 480px;
  border-radius: 10px;
}
.edrea-archive-blog__slider--item .trending-detail {
  width: calc(50% - 30px);
}
.edrea-archive-blog__slider--item .trending-detail .trending-title {
  font-size: 40px;
  color: rgb(80, 80, 80);
}
.edrea-archive-blog__slider--item .trending-detail .trending-excerpt {
  font-size: 25px;
  margin-bottom: 60px;
  line-height: 1.4;
}
.edrea-archive-blog__slider--item .trending-detail .trending-button {
  padding: 10px 20px;
  color: #FFF;
  background-color: #005792;
  text-decoration: none;
  font-size: 20px;
  border-radius: 5px;
  transition: 0.3s;
}
.edrea-archive-blog__slider--item .trending-detail .trending-button:hover {
  background-color: #53CDE2;
}
.edrea-archive-blog__wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  gap: 30px 30px;
  grid-template-areas: ". . ." ". . ." ". . .";
  justify-content: space-evenly;
  justify-items: center;
  align-content: space-evenly;
  align-items: center;
}
.edrea-archive-blog .edrea-load-more {
  display: flex;
  justify-content: center;
}
.edrea-archive-blog .edrea-load-more button {
  background-color: #005792;
  color: #FFF;
  width: 160px;
  padding: 10px;
  font-size: 17px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.edrea-single-post {
  padding: 30px 0;
}
.edrea-single-post__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.edrea-single-post__wrapper--left {
  width: calc(72% - 25px);
}
.edrea-single-post__wrapper--left p, .edrea-single-post__wrapper--left li {
  font-size: 17px;
  line-height: 1.6;
  color: rgba(0, 0, 0, 0.8509803922);
}
.edrea-single-post__wrapper--right {
  width: calc(28% - 25px);
  padding: 20px 0;
}

.edrea-header {
  padding: 30px 0;
  background-color: #005792;
}
.edrea-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.edrea-header__wrapper .site-branding {
  width: calc(27% - 20px);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.edrea-header__wrapper .site-branding .site-description {
  margin: 0;
  color: #FFF;
  font-style: italic;
  margin-left: 20px;
  font-weight: 600;
}
.edrea-header__wrapper .main-navigation {
  width: calc(46% - 20px);
  display: flex;
  align-items: center;
}
.edrea-header__wrapper .main-navigation ul {
  list-style: none;
  display: flex;
  padding-left: 0;
}
.edrea-header__wrapper .main-navigation ul li {
  margin: 0 15px;
}
.edrea-header__wrapper .main-navigation ul li a {
  color: #FFF;
  text-decoration: none;
  font-size: 17px;
  font-weight: 500;
  transition: 0.3s;
}
.edrea-header__wrapper .main-navigation ul li a:hover {
  color: #53CDE2;
}
.edrea-header__wrapper .site-search {
  width: calc(27% - 20px);
  text-align: right;
}
.edrea-header__wrapper .site-search input {
  border: none;
  font-size: 15px;
  padding: 10px;
  border-radius: 5px;
}
.edrea-header__wrapper .site-search input:focus {
  outline: none;
}
.edrea-header__wrapper .site-search input[type=submit] {
  background-color: #53CDE2;
  color: #FFF;
  font-weight: 500;
  padding: 10px 20px;
  cursor: pointer;
  transition: 0.3s;
}
.edrea-header__wrapper .site-search input[type=submit]:hover {
  background-color: #79D1C3;
}

.edrea-footer {
  background-color: #005792;
  padding-top: 40px;
  border-top: 15px solid #53CDE2;
  margin-top: 60px;
}
.edrea-footer__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.edrea-footer__wrapper ul {
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 30px;
}
.edrea-footer__wrapper ul li {
  list-style: none;
  margin: 0 17px;
}
.edrea-footer__wrapper ul li a {
  text-decoration: none;
  color: #FFF;
  font-size: 17px;
  font-weight: 500;
  transition: 0.3s;
}
.edrea-footer__wrapper ul li a:hover {
  color: #53CDE2;
}
.edrea-footer__wrapper .edrea-credit {
  padding-bottom: 40px;
}
.edrea-footer__wrapper .edrea-credit p {
  color: #FFF;
  margin: 0;
  font-size: 14px;
}
.edrea-footer__wrapper .edrea-credit p a {
  color: #53CDE2;
  text-decoration: none;
  font-weight: 600;
}
.edrea-footer__wrapper .edrea-credit .author {
  font-size: 12px;
  text-align: center;
  margin-top: 10px;
}
.edrea-footer__wrapper .edrea-credit .author a {
  font-weight: 400;
}

.edrea-card {
  box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.08);
  width: calc(33% - 30px);
  margin-right: 30px;
  border-radius: 8px;
  margin-bottom: 30px;
  transition: 0.3s;
}
.edrea-card:hover {
  transform: translateY(-5px);
}
.edrea-card__thumbnail {
  position: relative;
  padding: 10px;
}
.edrea-card__thumbnail img {
  width: 100%;
  border-radius: 8px;
}
.edrea-card__thumbnail .article-date {
  position: absolute;
  left: 10px;
  bottom: 30px;
  background-color: rgba(0, 0, 0, 0.301);
  padding: 7px;
  padding-left: 20px;
}
.edrea-card__thumbnail .article-date span {
  color: #FFF;
}
.edrea-card__thumbnail .article-category {
  position: absolute;
  top: 20px;
  right: 10px;
  display: flex;
}
.edrea-card__thumbnail .article-category .cat {
  text-decoration: none;
  margin: 0 3px;
}
.edrea-card__thumbnail .article-category .cat p {
  padding: 5px;
  text-decoration: none;
  color: #FFF;
  font-size: 10px;
  margin: 0;
  border-radius: 3px;
  font-weight: 600;
  text-transform: uppercase;
}
.edrea-card__detail {
  padding: 0 20px;
  padding-top: 0;
  position: relative;
}
.edrea-card__detail a {
  color: #06283D;
  text-decoration: none;
}
.edrea-card__detail a h1 {
  margin: 0;
  color: #005792;
}
.edrea-card__detail p {
  line-height: 1.6;
  color: rgb(80, 80, 80);
}
.edrea-card__detail p a {
  color: #e77221;
  font-weight: 600;
}

.edrea-breadcrumbs {
  display: flex;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  padding-left: 0 !important;
}
.edrea-breadcrumbs li {
  list-style: none;
  font-size: 12px;
}
.edrea-breadcrumbs li a {
  color: #e77221;
  text-decoration: none;
  font-size: 12px;
  font-weight: 600;
}
.edrea-breadcrumbs li span {
  margin: 0 10px;
}

.edrea-widget-area h2, .edrea-widget-area h3, .edrea-widget-area h4 {
  color: #005792;
  font-weight: 600;
  text-transform: uppercase;
  font-weight: 700;
}
.edrea-widget-area a {
  color: #06283D;
  transition: 0.3s;
}
.edrea-widget-area a:hover {
  color: #e77221;
}
.edrea-widget-area li {
  margin-bottom: 10px;
}

.nav-links {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.nav-links .nav-previous, .nav-links .nav-next {
  margin: 0 15px;
  background-color: #005792;
  padding: 10px 25px;
  border-radius: 5px;
  transition: 0.3s;
}
.nav-links .nav-previous:hover, .nav-links .nav-next:hover {
  background-color: #e77221;
}
.nav-links .nav-previous a, .nav-links .nav-next a {
  text-decoration: none;
  color: #FFF;
  font-weight: 600;
  font-size: 14px;
}